import React from 'react';
import { Panel, Div, PanelHeader, Group, PanelHeaderButton, Button} from '@vkontakte/vkui'
import FaveText from './FaveText';
import './Persik.css'

const Fave = props => {

	let items = Array.from({ length: 50 })

	return (
		<Panel id={props.id}>
		<PanelHeader>Избранное</PanelHeader>

					<Group>
						<Div className='center'>
					Здесь находятся твои избранные лайфхаки!
							</Div>
						</Group>
							{props.zero && <Group><Div className='center'>У тебя нет избранных лайфхаков!</Div></Group>}

													{items.map((i, index) => (
													<FaveText {...props} key={index} id={index}/>
											 		))}
													{ props.snackbar }
						</Panel>

	);
}



export default Fave;
