import React from 'react';
import { Panel, Div, platform,  PanelHeaderButton, IOS, PanelHeader, Group} from '@vkontakte/vkui'
import Text from './Text';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './Persik.css'

const osname = platform();
let items = Array.from({ length: 50 })

const Mother = props => (
	<Panel id={props.id}>
	<PanelHeader left={<PanelHeaderButton onClick={() => props.back()}>
			{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
		</PanelHeaderButton>}>
					 День матери
					</PanelHeader>
					<Group>
					<Div className='center'>
				Здесь ты можешь найти поздравления ко Дню матери!
						</Div>
						</Group>
												{items.map((i, index) => (
											<Text  {...props} key={index} id={index} name='mother'/>
										 		))}
													{ props.snackbar }
					</Panel>

);

export default Mother;
