  'Поздравляю с днем рождения! Пусть все мечты сбываются, желания исполняются, цели достигаются, здоровье улучшается и деньги прибавляются! Счастья, радости и душевной гармонии, блистательных удач, незабываемых впечатлений и головокружительных успехов, настоящей любви, верных друзей и яркой радуги эмоций!',
  'С днем рождения! Пусть жизнь будет беспрерывным потоком счастливых дней и прекрасных мгновений. Желаю назад оглядываться только лишь с хорошими воспоминаниями, вперёд смотреть с уверенностью в собственных силах и доброй надеждой, а в настоящем всегда оставаться замечательным человеком с любящим сердцем и открытой душой.',
  'Поздравляю с днем рождения! Кроме стандартных пожеланий — счастья, здоровья и всего наилучшего — я желаю, чтобы сбывались все мечты, чтобы каждый день приносил много приятных неожиданностей, чтобы Вас окружали только добрые и нужные люди. А еще везения. Везения во всём и всегда. И чтобы каждое начатое дело заканчивалось успешно!',
  'Поздравляю от души с днем рождения! Желаем много любви и счастья, чтобы судьба чаще дарила приятные сюрпризы. Пусть жизнь будет интересной и насыщенной, пусть всегда хватает сил для достижения желаемых высот!',
  'Поздравляю с днём рождения тебя! Желаю прекрасной и удивительной жизни, океан безумной любви, бесконечного счастливого времени, только крепкого здоровья и отличного настроения.',
  'Поздравляю с днем рождения! Желаем здоровья, удачи, любви, везения, мира, добра, улыбок, благополучия. Пусть все мечты исполняются. Пусть жизнь будет долгой и гладкой, полной ярких и запоминающихся событий!',
  'От всей души прими мои самые искренние поздравления с днем рождения! Я желаю тебе крепкого здоровья, финансовой стабильности, гармонии во всем, любви во всех ее проявлениях, мирного неба над головой. Пусть по жизни сопутствует удача и успех, тебя окружают близкие по духу люди, а каждый твой день сияет новыми радужными красками!',
  'Поздравляю тебя с днём рождения! Желаю не болеть, не грустить, много улыбаться, сиять от счастья, с годами только хорошеть, верить в лучшее и в мечту, которая несомненно сбудется. Пусть удача будет твоим путеводителем, перед тобой открываются золотые горы благополучия и сладких грёз. Волшебные моменты пусть озаряют твой взгляд, любовь всегда будет рядом и доставляет тебе только удовольствие.',
  'Поздравляю с днем рождения! Желаю, чтобы твоя жизнь была яркой и восхитительной, любимые дарили внимание, радость, тепло и ласку, друзья окружали искренним пониманием и поддержкой, коллеги уважали и дорожили. Пусть всё, что казалось несбыточным, сбудется, и самое желанное пусть произойдет!',
  'Чудесный, солнечный человечек, я поздравляю тебя с днём рождения! Пусть в любой день и в любую погоду у тебя всё будет замечательно, пусть всегда буду те, кому можно подарить свои крепкие объятия, пусть всегда будут те, кто наполнит твою жизнь светом счастья.',
  'С днем рождения! Пусть твой дом наполняет счастье и тепло, твою душу — гармония и спокойствие. В сердце пускай живет любовь, в мыслях — позитив. Пусть судьба преподносит яркие подарки и радостные события, а в жизни всегда будет хорошая погода!',
  'С днём рождения желаю счастье, здоровье, любви, и всего всего хорошего в жизни!',
  'Всё, что хочется пожелать в этот день, — это чтобы сбывалось то, о чём мечтается перед сном. Чтобы были здоровы и счастливы самые близкие люди. Чтобы друзья поддерживали в трудную минуту, а радовались вместе — в светлую. Пусть следующий год будет полон улыбок и слез от смеха. Желаю побольше памятных событий!',
  'Поздравляю с днем рождения! В этот замечательный день желаю тебе крепкого здоровья, верных и преданных друзей, безграничного счастья, безразмерных доходов, сумасшедшей любви и побольше радостных дней! Хорошего тебе настроения и отличного праздника!',
  'Поздравляю с днем рождения! Искренне желаю самых лучших дней, приносящих с собой радужное настроение, удачу, любовь и бесконечное счастливое время. Пускай здоровье только крепнет, красота души не увядает, а жизнь преподносит чудесные подарки. Пусть живется просто как в сказке.',
  'Поздравляю с днем рождения! Желаю, чтобы каждый день жизни был наполнен легкостью, здоровьем, новыми возможностями, любовью, искренностью и успехом. Будьте безгранично счастливы, впитывайте положительные эмоции и излучайте благополучие!',
  'Поздравляю с днем рождения! Но желать тебе здоровья и счастья, думаю, не стоит, так как все уже давно этого пожелали, и это обязательно будет! Я пожелаю тебе, чтобы у тебя было то, что именно тебе нужно. То, чего хочешь ты и только ты! Ведь только ты знаешь, что тебе нужно для самого главного — для счастья. С днем рождения!'
